.App {
  padding: 130px 0 0;
}

.QuoteBox {
  max-width: 500px;
  border-radius: 12px;
  padding: 50px 70px 40px;
  margin: 0 auto 50px ;
  color: #2ea78c;
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.75);
  border: 1px solid rgba(209, 213, 219, 0.3);
}
@media (max-width: 670px) {
  .QuoteBox {
    padding: 40px;
    margin: 0 15px 70px ;
  }
}

.Text h1 {
  text-align: left;
  margin-top: 0;  
  margin-left: 10px;
}

.double-quote-icon {
  width: 40px;
  margin: 10px 0 10px -10px;
}

.Author {
  text-align: right;
}

.ButtonNewQuote {
  font-size: 16px;
  padding: 15px 50px;
  margin-top: 10px;
  border: none;
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.75);
  cursor: pointer;
  background-color: #55acee; 
}

.LinkTweetQuote {
  position: absolute;
  left: 50%;
  top: 50px;
  padding: 15px;
  z-index: 1;
}

.LinkTweetQuote p {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.8);
  position: absolute;
  top: 100px;
  left: -30%;
}

.tweet-quote {
  position: relative;
  left: -50%;
}

svg {
  fill: #55acee;
}

svg:hover, svg:focus {
  fill: rgba(255, 255, 255, 0.75);
  padding: 10px;
  border-radius: 12px;
  background: #55acee;
}

footer {
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  position: fixed;
  bottom: 0;
  left: 50%;
}

footer p {
  position: relative;
  left: -50%;
}

footer a {
  font-weight: 600;
  color: rgba(255, 255, 255, 0.5);
}

.center {
  text-align: center;
}